import React, { Component } from 'react';
import { Col, Form } from 'antd';
import { BtnSearch } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'FormIntegrateTraining' })
class FormIntegrateTraining extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      this.props.dataForm(values);
      udpateQueryParams(values);
    });
  };
  render() {
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn H!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Khoa!',
          },
        ],
      },
      courseId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn Kha',
          },
        ],
      },
    };
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <FilterPathEducate form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
        <YearSemesterInput form={this.props.form} />
        <Col xs={24} sm={12} md={6}>
          <div className="btn-group">
            <BtnSearch htmlType="submit" />
          </div>
        </Col>
      </Form>
    );
  }
}

export default FormIntegrateTraining;



